.todo {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid #555;
  color: #112d49;
  background-color: #fbfef9;
  /* user-select: none; */
  gap: 10px;
  /* max-width: 600px; */
  /* margin: 0 auto; */
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

/* @media (any-hover: hover) {
  .todo:hover {
    background-color: #cacec7;
  }
} */
/* .todo:active {
  background-color: #cacec7;
} */

.todoText {
  /* width: 100%; */
  flex: 1 1 100%;
  text-align: left;
  /* word-wrap: wrap; */
  /* white-space: wrap; */
  word-break: keep-all;
}

.todoIcon {
  justify-self: center;
  font-size: 30px;
  flex: 0 0 30px;
  color: teal;
}

.completedTodo {
  background-color: unset;
  border-color: gray;
  color: gray;
}

.todo.completedTodo .todoIcon,
.todo.completedTodo .checkIcon,
.todo.completedTodo .deleteIcon {
  color: gray;
}

.checkIcon,
.deleteIcon {
  flex: 0 0 45px;

  cursor: pointer;
  color: lightgrey;
  padding: 0 7px;
  font-size: 45px;
  transition: color 0.3s;
}

/* .checkIcon:hover, */
/* .deleteIcon:hover { */
/* color: blue !important; */
/* transform: scale(1.3); */
/* } */

.checkIcon:hover {
  color: green;
}

.deleteIcon:hover {
  color: red;
}

@media (max-width: 400px) {
  .todo {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .todoText {
    text-align: center;
  }
}
