.button {
  /* margin-left: 20px; */
  /* margin: 0 auto; */
  height: 50px;
  cursor: pointer;
  background-color: beige;
  font-size: 1.5rem;
  color: #000;
  display: inline-flex;
  justify-content: center;
  /* flex-direction: column; */
  align-items: center;

  padding: 10px 15px;
  border: none;
  border-radius: 5px;
}

.button:hover {
  background-color: rgb(240, 240, 155);
}

.button:disabled {
  background-color: lightgray;
}
